.gallery-block section {
  position: relative;
  top: 30px;
}

.category-block {
  cursor: pointer;
}

.category-block .active {
  opacity: 1;
}

.category-title {
  text-align: center;
  position: relative;
  top: 90px;
}

.category-img {
  width: 250px;
  height: 160px;
  opacity: .3;
}

.category-block:hover .category-img {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 1;
}


